.borderStart {
  width: calc(100% - 0px);
  height: calc(100% - 0px);
  -webkit-transform: translate(0px, 0px);
          transform: translate(0px, 0px)
}
.borderEnd {
  width: calc(100% - 6px);
  height: calc(100% - 6px);
